@import '../../style/style.scss';

.wrap {

  .markerWrap {
    height: 44px;
    position: relative;
  }

  .marker {
    transform: perspective(40px) rotateX(20deg) rotateZ(-45deg);
    transform-origin: 50% 50%;
    border-radius: 50% 50% 50% 0;
    padding: 0 3px 3px 0;
    width: 30px;
    height: 30px;
    background: $color-1;
    position: absolute;
    //left: 67%;
    margin-left: -15px;
    box-shadow: -1px 1px 4px #222;

    transition: left #{$animation};
  }

  .markerNumber {
    position: absolute;
    width: 20px;
    text-align: center;
    margin-left: -10px;
    color: #222;
    font-size: 16px;
    font-weight: bold;
    //left: 67%;
    top: 5px;
    text-shadow: none;

    transition: left #{$animation};
  }

  .bar {
    background: $color-1;
    background: linear-gradient(90deg, $color-1 0%, $color-3 100%);
    height: 10px;
    border-radius: 10px;
    display: flex;
    overflow: hidden;

    .barProgress {
      background-color: transparent;
      box-shadow: inset 0 0 2px #222;
      //width: 67%;

      transition: width #{$animation};
    }
    .barGray {
      box-shadow: inset 0 0 4px #222;
      background-color: #444;
      flex: 1;
    }
  }

  .barTitle {
    margin-top: 6px;
    color: white;
    font-weight: bold;

    span {
      font-size: 18px;
      color: $color-1;
      padding-left: 6px;
    }
  }

}
